body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: rgb(241, 241, 241);
}

.cartWidget {
    display: flex;
    align-items: center;
    margin-left: auto;
    cursor: pointer;
    padding-right: 1rem;
}

.cartWidget p {
    display: inline;
}

.cartWidget div{
    width: 50px;
    height: 50px;
    
}

img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.cart{
    height: 30px;
    color:aliceblue;
}
.cart img{
    height: 100%;
}

.menuLink{
    text-decoration: none;
    color: hsl(240, 35%, 56%);
}
.item {
  color:black;
  text-decoration: none;
}


.card {
    padding: 1rem;
    background: rgb(187, 210, 210);
    border-radius: 6px;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.avatar {
    width: 100px;
    height: 100px;
    border-radius: 6px;
    overflow: hidden;
    margin: 0 auto;
}

.avatar img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.itemList {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: space-around;
  }
  
  .greeting{
    font-weight: bolder;
    font-size: 3rem;
    color: rgb(163, 185, 222);
  }
.itemDiv{
display: flex;
justify-content: center;
margin-top: 10px;


}
p{
    width: 40px;
    display: inline-block;
}

p img{
    object-fit: contain;
    width: 90%;
    cursor: pointer;

}

.addtoCart{
    background-color: black;
    padding: 2px;   
    color: antiquewhite;
    cursor: pointer;
}

.endBuy{
    background-color: black;
    padding: 3px;   
    color: antiquewhite;
    cursor: pointer;
    text-decoration: none;
    
    
}

.item div{
    padding-top: 5px;
}
.descr{
    font-style: oblique;
}
.button{
    background-color: black;
    padding: 3px;   
    color: antiquewhite;
    cursor: pointer;
    text-decoration: none;
    
    
}

.item div{
    padding-top: 5px;
}

.itemList {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: space-around;
  }
  
  .menu{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
  
  .menu div{
    width: 90wv;
  }
.formDiv{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    
}

label{
    width: 150px;
    margin-bottom: 10px;
}
.title {
    font-weight: bold;
}

span {
    font-style: oblique;
}
